/*************** CONTACT FORM *****************/
const form = document.querySelector( ".contact-content__form" )
const submit_button = document.querySelector( ".contact-content__form button[type='submit']" )
const formElements = [
	...document.querySelectorAll( ".contact-content__form--user-data input" ),
	document.querySelector( ".contact-content__form--message" )
]

form.addEventListener( "submit", ( e ) => {
	e.preventDefault()
	const formData = new FormData( form )
	const message = {
		email: formData.get( "email" ),
		name: formData.get( "name" ),
		message: formData.get( "message" )
	}
	formElements.forEach( el => {
		el.classList.remove( "send" )
		el.classList.add( "send" )
		setTimeout( () => el.classList.remove( "send" ), 1000 )
		el.value = "";
	} )
	submit_button.classList.add( "message-sent", "add-checkbox" )
	setTimeout( () => submit_button.classList.remove( "message-sent" ), 3500 )
} )